<template>
  <div id="app">
    <div class="header">
      <ul class="header-button-left" v-if="step != 1" v-on:click="step--">
        <li>Cancel</li>
      </ul>
      <ul class="header-button-right" v-if="step == 2" v-on:click="step++">
        <li>Next</li>
      </ul>
      <ul class="header-button-right" v-if="step == 3" v-on:click="publish">
        <li>Submit</li>
      </ul>
      <img src="./assets/vuestagram.png" class="logo" />
    </div>
    <Body
      v-bind:dataposts="dataposts"
      v-bind:step="step"
      v-bind:image="image"
      v-bind:filter="filter"
      v-on:caption="caption = $event"
    />
    <!-- <div class="sample-box">임시 박스</div> -->
    <div class="footer">
      <ul class="footer-button-plus" v-if="step == 1">
        <input
          type="file"
          id="file"
          class="inputfile"
          v-on:change="uploadImage"
        />
        <label for="file" class="input-plus">+</label>
      </ul>
    </div>
    <button v-on:click="more">더보기</button>

    <p>{{ $store.getters.NAME }}</p>
    <p>{{ $store.getters.AGE }}</p>
    <button v-on:click="$store.dispatch('SET_NAME', 'Emily')">
      Modify Name
    </button>
    <button v-on:click="$store.commit('PLUS_AGE')">
      Plus Age
    </button>
  </div>
</template>

<script>
import Axios from "axios";
import EventBus from "./EventBus";
import Body from "./components/Body";
import dataposts from "./assets/postdata.js";
export default {
  name: "app",
  components: {
    Body
  },
  methods: {
    publish() {
      this.step = 1;
      // 전통적인 방식(HTML, JS를 건드려서 고치겠다)을 버려라!
      // Data 중심이다! (즉, 변경점이 있으면 Data를 고쳐라!)
      // 여기선 dataposts에 데이터를 추가해주면 되는 것!
      let mydata = {
        name: "newuser",
        // http://placeimg.com/
        // https://placeimg.com/{width}/{height}/{topic} => 가로 / 세로 / 주제 넣으면 자동으로 랜덤하게 주제 관련 이미지 받아옴
        userImage: "https://placeimg.com/100/100/hawk",
        postImage: this.image,
        likes: 0,
        date: new Date(),
        liked: false,
        caption: this.caption,
        filter: "perpetua"
      };
      // unshift는 배열 앞에, push는 배열 뒤에
      this.dataposts.unshift(mydata);
    },
    uploadImage(e) {
      // 1. filter 선택 화면을 보여줌 (step 2로 넘어간다는 의미)
      // 2. FileReader로 image(URL)를 보여줌
      let file = e.target.files;
      let reader = new FileReader();
      reader.readAsDataURL(file[0]);
      reader.onload = e => {
        this.image = e.target.result;
        this.step = 2;
      };
    },
    more() {
      Axios.get("/more.json").then(result => {
        console.log(result.data);
        this.dataposts.push(result.data);
      });
    }
  },
  mounted() {
    EventBus.$on("select-filter", filter => {
      console.log(filter);
      this.filter = filter;
    });
  },
  data() {
    return {
      dataposts: dataposts,
      step: 1,
      image: "",
      caption: "",
      filter: ""
    };
  }
};
</script>

