<template>
  <!-- index.html에 첨부한 cssgram의 class -->
  <div
    v-bind:class="['filter-item', filter]"
    v-bind:style="{ backgroundImage: `url(${image})` }"
    v-on:click="clickFilter"
  >
    <p>{{ filter }}</p>
    <slot></slot>
  </div>
</template>

<script>
import EventBus from "./../EventBus";
export default {
  name: "FilterBox",
  props: {
    image: String,
    filter: String
  },
  components: {},
  methods: {
    clickFilter() {
      EventBus.$emit("select-filter", this.filter);
    }
  },
  data() {
    return {};
  }
};
</script>

<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
}
</style>
