// vuex의 저장공간!
// vuex는 redux와 유사

import Vue from "vue";
import Vuex from "vuex";
import Axios from "axios";
Vue.use(Vuex);
export let store = new Vuex.Store({
  // state...상태가 아니라 그냥 data라고 생각해야 이해가 쉽다!
  // state는 직접 건들 수 없고, 아래의 getters, mutations 안에 등록한 함수를 통해서만 해야한다!
  state: {
    name: "Johnson",
    age: 28,
    likes: 36
  },
  getters: {
    // state를 바깥에서 내보내려면, 여기 안에서 반환 프로세스를 함수 형식으로 만들어 주어야한다!
    NAME(state) {
      return state.name;
    },
    AGE(state) {
      return state.age;
    },
    LIKES(state) {
      return state.likes;
    }
  },
  mutations: {
    // 2번째 이상의 인자를 받으려면 해당 컴포넌트에서 commit 메소드를 이용해야한다.
    // 'ajax 요청을 사용하려면' dispatch로 actions 안의 함수 commit을 통해 해야한다
    // 지금 이거 존나 이상하니 복습하면서 고쳐보기
    SET_NAME(state, name) {
      state.name = name;
    },
    PLUS_AGE(state) {
      state.age++;
    },
    PLUS_LIKES(state) {
      state.likes++;
    }
  },
  actions: {
    // 'ajax 요청을 사용하려면' dispatch로 actions 안의 함수 commit을 통해 해야한다
    // 컴포넌트 -> dispatch -> actions -> commit -> mutations
    // dispatch 함수 사용
    // 지금 이거 존나 이상하니 복습하면서 고쳐보기
    SET_NAME(context, data) {
      Axios.get("/more.json").then(data => {
        context.commit("SET_NAME", data);
      });
    }
  }
});
